<template>
  <div>
    <Row style="border-bottom: 2px solid #41b883;padding-bottom: 16px;margin-bottom: 26px;">
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;" @click="addTag()">添加标签</Button>
      </div>
      </Col>
      <Col span="12">
      <div>
        <Input placeholder="请输入关键字：标签名称" style="width: 500px"></Input>
      </div>
      </Col>
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;">查询</Button>
      </div>
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <div  style="margin-top:20px;">
        <Table border :columns="columns1" :data="data1"></Table>
      </div>
      </Col>
    </Row>

    <Modal
      v-model="modal1"
      title="添加标签"
      width="600"
      :styles="{top: '20px'}"
      @on-ok="ok"
      @on-cancel="cancel">
      <Form :label-width="80" ref="formValidate" :model="formLable">
        <FormItem label="标签名称">
          <Input type="text" v-model="formLable.tagName"  placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="关联用户">
          <Transfer
            :data="data"
            :target-keys="targetKeys"
            :render-format="render"
            @on-change="onChange"></Transfer>
        </FormItem>
        <FormItem label="描述">
          <Input type="textarea" v-model="formLable.mark" placeholder="请输入"></Input>
        </FormItem>
      </Form>
    </Modal>
    <Modal
      v-model="modal2"
      title="添加标签"
      width="600"
      :styles="{top: '20px'}"
      @on-ok="ok"
      @on-cancel="cancel">
      <Form :label-width="80" ref="formValidate" :model="formLable">
        <FormItem label="标签名称">
          <Input type="text" v-model="formLable.tagName"  placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="关联用户">
          <Transfer
            :data="data"
            :target-keys="targetKeys2"
            :render-format="render"
            @on-change="onChange"></Transfer>
        </FormItem>
        <FormItem label="描述">
          <Input type="textarea" v-model="formLable.mark" placeholder="请输入"></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
  export default {
    name: 'Label',
    components: {
    },
    data () {
      return {
        modal1: false,
        modal2: false,
        data: [
          { 'key': '1', 'label': 'user1' },
          { 'key': '2', 'label': 'user2' },
          { 'key': '3', 'label': 'user3' },
          { 'key': '4', 'label': 'user4' },
          { 'key': '5', 'label': 'user5' },
          { 'key': '6', 'label': 'user6' },
          { 'key': '7', 'label': 'user7' },
          { 'key': '8', 'label': 'user8' },
          { 'key': '9', 'label': 'user9' }
        ],
        targetKeys: [],
        targetKeys2: [],
        formLable: {
          tagName: '',
          mark: ''
        },
        columns1: [
          {
            title: '标签名称',
            key: 'tagName'
          },
          {
            title: '统计用户',
            key: 'users'
          },
          {
            title: '创建时间',
            key: 'time'
          },
          {
            title: '操作',
            key: 'option',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.editUser(params)
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '删除用户',
                        content: '<p>确认删除此用户吗？</p>',
                        onOk: () => {
                          this.$Message.info('点击了确定')
                        },
                        onCancel: () => {
                          this.$Message.info('点击了取消')
                        }
                      })
                    }
                  }
                }, '删除')
              ])
            }
          }
        ],
        data1: [
          {
            tagName: '信息安全服务部',
            users: 'user1 user2 user3',
            time: '2017/11/10 11:39:00'
          },
          {
            tagName: '信息安全服务部',
            users: 'user4 user5 user6',
            time: '2017/11/10 12:00:10'
          },
          {
            tagName: '信息安全服务部',
            users: 'user7 user8 user9',
            time: '2017/11/8 11:55:00'
          }
        ]
      }
    },
    methods: {
      addTag () {
        this.targetKeys = []
        this.formLable = {
          tagName: '',
          mark: ''
        }
        this.modal1 = true
      },
      editUser (params) {
        console.log(params.row)
        console.log(params.row.tagName)
        var strs = params.row.users
        var users = [] // 定义一数组
        users = strs.split(' ') // 字符分割
        for (var i = 0; i < users.length; i++) {
          for (var j = 0; j < this.data.length; j++) {
            if (users[i] === this.data[j].label) {
              this.targetKeys2.push(this.data[j].key)
            }
          }
        }
        this.modal2 = true
        this.formLable = {
          tagName: params.row.tagName,
          mark: '这是一些备注信息'
        }
      },
      ok () {
        this.targetKeys2 = []
        this.$Message.info('点击了确定')
      },
      cancel () {
        this.targetKeys2 = []
        this.$Message.info('点击了取消')
      },
      render (item) {
        return item.label
      },
      onChange (newTargetKeys, direction, moveKeys) {
        console.log(direction)
        console.log(moveKeys)
        console.log(newTargetKeys)
        this.targetKeys = newTargetKeys
      }
    }
  }
</script>



