<template>
  <Row>
    <Col span="2">
      <div class="sidebar">
          <Menu theme="dark" width="100%" class="menu">
            <router-link :to="item.path" v-for="(item,index) in workorderchildrenroute">
              <Menu-item :name="item.name" class="subM">
                {{item.name}}
              </Menu-item>
            </router-link>
          </Menu>
          </Menu>
      </div>
    </Col>
    <Col span="21" offset="3">
      <div>
      <router-view></router-view>
      </div>
    </Col>
  </Row>
</template>

<script>
  export default {
    name: 'workorder',
    components: {
    },
    data: function () {
      return {
        childrenroutes: [],
        workorderchildrenroute: []
      }
    },
    mounted () {
      this.getRoutes()
    },
    methods: {
      getRoutes () {
        this.childrenroutes = this.$router.options.routes[0].children
        this.workorderchildrenroute = this.childrenroutes[2].children
      }
    }
  }
</script>



