// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store/store'
import router from './router'
import vueResource from 'vue-resource'
import iView from 'iview'
import '!style-loader!css-loader!less-loader!./theme/index.less'

Vue.config.productionTip = false

Vue.use(iView)
Vue.use(vueResource)
Vue.http.options.emulateJSON = false

Vue.http.get('../static/test.json').then(function (data) {
  Vue.data = data.body
  console.log(Vue.data)
}).catch(function (response) {
  console.log(response)
})

let user = false
if (!user) {
  window.location.pathname = '/home'
  router.beforeEach((to, from, next) => {
    if (to.path === '/') {
      next({path: '/homepage'})
    } else if (to.path === '/leaknav') {
      next({path: '/leaknav/leak-1'})
    } else if (to.path === '/workordernav') {
      next({path: '/workordernav/workorder-1'})
    } else if (to.path === '/systemnav') {
      next({path: '/systemnav/system-1'})
    } else {
      next()
    }
  })
  /* eslint-disable no-new */
  new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: {App}
  })
} else {
  window.location.href = '#'
  window.location.pathname = 'login.html'
}



