<template>
  <div>
    <Row style="border-bottom: 2px solid #41b883;padding-bottom: 16px;margin-bottom: 26px;">
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;" @click="addDict()">添加字典</Button>
      </div>
      </Col>
      <Col span="12">
      <div>
        <span>类型：</span>
        <Select v-model="model1" style="width:120px">
          <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        <span style="margin-left: 40px;">描述：</span><Input placeholder="请输入..." style="width: 300px"></Input>
      </div>
      </Col>
      <Col span="6">
      <div>
        <Button type="success" style="width:100px;">查询</Button>
      </div>
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <div style="margin-top:20px;">
        <Table border :columns="columns0" :data="data0"></Table>
      </div>
      </Col>
    </Row>
    <Modal
      v-model="modal0"
      title="添加字典"
      @on-ok="ok"
      @on-cancel="cancel"
      :styles="{top: '20px'}">
      <Form :label-width="80" ref="formValidate" :model="formDict">
        <FormItem label="键值">
          <Input type="text" v-model="formDict.key"  placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="标签">
          <Input type="text" v-model="formDict.tag" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="类型">
          <Input type="text" v-model="formDict.type" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="描述">
          <Input type="text" v-model="formDict.describe" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="排序">
          <Input type="text" v-model="formDict.order" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="备注">
          <Input type="textarea" v-model="formDict.mark" placeholder="请输入"></Input>
        </FormItem>
      </Form>
    </Modal>
    <Modal
      v-model="modal1"
      title="编辑角色"
      @on-ok="ok"
      @on-cancel="cancel"
      :styles="{top: '20px'}">
      <Form :label-width="80" ref="formValidate" :model="formDict">
        <FormItem label="键值">
          <Input type="text" v-model="formDict.key"  placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="标签">
          <Input type="text" v-model="formDict.tag" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="类型">
          <Input type="text" v-model="formDict.type" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="描述">
          <Input type="text" v-model="formDict.describe" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="排序">
          <Input type="text" v-model="formDict.order" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="备注">
          <Input type="textarea" v-model="formDict.mark" placeholder="请输入"></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
  export default {
    name: 'Dict',
    components: {
    },
    methods: {
      addDict () {
        this.formDict = {}
        this.modal0 = true
      },
      ok () {
      },
      cancel () {
      }
    },
    data () {
      return {
        modal0: false,
        modal1: false,
        checkedNodes: '',
        userid: '',
        username: '',
        columns0: [
          {
            title: '键值',
            key: 'key'
          },
          {
            title: '标签',
            key: 'tag'
          },
          {
            title: '类型',
            key: 'type'
          },
          {
            title: '描述',
            key: 'describe'
          },
          {
            title: '排序',
            key: 'order'
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.modal1 = true
                    }
                  }
                }, '添加键值'),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.modal1 = true
                      console.log(params.row)
                      this.formDict = {
                        key: params.row.key,
                        tag: params.row.tag,
                        type: params.row.type,
                        describe: params.row.describe,
                        order: params.row.order,
                        mark: '这是一个备注'
                      }
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '确认删除',
                        content: '<p>删除后无法恢复</p><p>确认要删除该条数据吗？</p>',
                        onOk: () => {
                          this.$Message.info('点击了确定')
                        },
                        onCancel: () => {
                          this.$Message.info('点击了取消')
                        }
                      })
                    }
                  }
                }, '删除')
              ])
            }
          }
        ],
        data0: [
          {
            key: '11',
            tag: '应用漏洞攻击',
            type: 'attack_type',
            describe: '应用漏洞攻击',
            order: '1'
          },
          {
            key: '12',
            tag: 'http会话攻击',
            type: 'sys_operation',
            describe: 'http会话攻击',
            order: '2'
          },
          {
            key: '13',
            tag: '注入攻击',
            type: 'error_code',
            describe: '注入攻击',
            order: '3'
          }
        ],
        formDict: {
          key: '',
          tag: '',
          type: '',
          describe: '',
          order: '',
          mark: ''
        },
        model1: 'all',
        typeList: [
          {
            value: 'all',
            label: '全部'
          },
          {
            value: 'attack_type',
            label: 'attack_type'
          },
          {
            value: 'error_code',
            label: 'error_code'
          },
          {
            value: 'sys_operation',
            label: 'sys_operation'
          }
        ]
      }
    }
  }
</script>



